
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page .panel .header {
  padding: 10px;
  background-color: #f6f6f6;
  border-radius: 3px 3px 0 0;
}
.page .panel .header .topic-tab {
  margin: 0 10px;
  color: #80bd01;
  text-decoration: none;
}
.page .panel .header .topic-tab.router-link-exact-active {
  background-color: #80bd01;
  color: #fff;
  padding: 3px 4px;
  border-radius: 3px;
}
.page .panel .header .topic-tab:hover {
  color: #08c;
}
.page .topic-list ul {
  padding: 0 10px;
}
.page .topic-list ul li {
  padding: 10px 0 10px 10px;
  border-top: 1px solid #f0f0f0;
  list-style: none;
}
.page .topic-list ul li:first-child {
  border-top: none;
}
.page .topic-list ul li .user-avatar img {
  width: 30px;
  height: 30px;
  border-radius: 3px;
}
.page .topic-list ul li .reply-count {
  display: inline-block;
  width: 70px;
  text-align: center;
  font-size: 12px;
  color: #b4b4b4;
}
.page .topic-list ul li .put-top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  color: #fff;
  font-size: 12px;
}
.page .topic-list ul li .topic-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  font-size: 12px;
}
.page .topic-list ul li .topic-title {
  color: #333;
}
.page .topic-list ul li .topic-title:visited {
  color: #888;
}
.page .topic-list ul li .last-active-time {
  margin-top: 9px;
  color: #778087;
  font-size: 12px;
}

.navbar {
  width: 100%;
  background: #444;
}
.navbar .navbar-inner {
  background: 0 0;
  border-radius: 0;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  width: 90%;
  margin: auto;
  padding: 5px;
}
.navbar .navbar-inner .brand {
  display: inline-block;
  width: 120px;
  height: 34px;
  line-height: 34px;
  color: #ccc;
}
.navbar .navbar-inner .brand img {
  width: 100%;
}
.navbar .navbar-inner .nav li {
  float: left;
}
.navbar .navbar-inner .nav li a {
  color: #ccc;
}
.navbar .navbar-inner .nav li a:hover {
  background: #444;
  color: #fff;
}
.navbar .navbar-inner .nav li .avatar {
  width: 30px;
  height: 30px;
  margin-top: 5px;
  padding: 0;
}
.navbar .navbar-inner .nav li .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.copyright {
  padding: 10px 0 20px;
  text-align: center;
}

