.page-body {
  margin-top: 65px;
  min-height: 80vh;
  background-color: #fff;
  border-radius: 3px;
}
.page-body .label {
  display: inline-block;
  width: 100%;
  margin-top: 15px;
  position: relative;
  left: 0;
  top: 0;
}
.page-body .label .txt {
  padding: 12px 5px;
  border: none;
  border-bottom: 1px solid #80bd01;
  background-color: transparent;
  width: 100%;
  font-size: 14px;
  color: #313131;
  outline: 0;
}
.page-body .label .button {
  display: inline-block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-radius: 3px;
  color: #fff;
  font-size: 16px;
  background-color: #80bd01;
  border: none;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
}
.page-body .label .button:hover {
  background-color: #79b300;
}
.page-body .label .file {
  position: absolute;
  top: 0;
  left: 0;
  height: 42px;
  width: 48%;
  outline: medium none;
  opacity: 0;
}
